.pagination {
  display: flex;
  justify-content: flex-end;
  width: 40%;
  margin: 80px auto;
}
.active-page {
  background-color: #76bb68;
  color: white;
}

.pagination p {
  cursor: pointer;
  margin-right: 10px;
  border-radius: 50%;
  padding: 10px 16px;
  font-weight: bold;
}
